  height: auto;
  display: flex;
  flex-direction: column;
}

.lazyload,
.lazyloading {
  opacity: 0;
}

.lazyloaded {
  opacity: 1;
  transition: opacity 300ms;
}

@font-face {
  font-family: "Montserrat";
  src: url("../assets/fonts/Montserrat-Regular.woff2") format("woff2"), url("../assets/fonts/Montserrat-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../assets/fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../assets/fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../assets/fonts/Montserrat-Bold.woff2") format("woff2"), url("../assets/fonts/Montserrat-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Stapel";
  src: url("../assets/fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../assets/fonts/Stapel-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

:root {
  --max-container-width: 181rem;
  --content-padding: 5.5rem;
  --primary-color: #148eff;
  --primary-color-hover: #1777cf;
  --montserrat: "Montserrat", sans-serif;
  --secondary-color: #ed5835;
  --secondary-color-hover: #d74928;
  --text-color: #011f3a;
}
@media only screen and (max-width: 640px) {
  :root {
    --content-padding: 1.5rem;
  }
}

html {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-size: 1.6rem;
  line-height: 1.2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: var(--montserrat);
  font-weight: 400;
  color: var(--text-color);
}

main {
  display: block;
}

ul,
ol {
  list-style: none;
}

ol {
  counter-reset: ordered-list;
}
ol > li {
  counter-increment: ordered-list;
}
ol > li::before {
  content: counter(ordered-list, decimal-leading-zero) ".";
}

input,
textarea {
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
  box-shadow: none;
  border-radius: 0;
  width: 100%;
  background-color: transparent;
  display: block;
}

input:not([type=checkbox]):not([type=radio]) {
  appearance: none;
  -webkit-appearance: none;
}

textarea {
  resize: none;
  box-shadow: none;
  display: block;
  width: 100%;
  overflow: auto;
  height: 20rem;
}
textarea::-webkit-scrollbar {
  background-color: white;
  width: 0.6rem;
}
textarea::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
}
textarea::-webkit-scrollbar-button {
  display: none;
}
textarea::-webkit-scrollbar-track {
  background-color: #f5f5f5;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

input[type=search] {
  -webkit-appearance: textfield;
}

input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  display: none;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
  -webkit-appearance: none;
  font: inherit;
  color: inherit;
  outline: none;
  display: inline-flex;
  position: relative;
  transition: color 0.2s;
}

a {
  background-color: transparent;
  text-decoration: none;
  transition: color 0.2s;
}
a:link, a:visited {
  color: inherit;
}

a:where(:not[class]):link, a:where(:not[class]):visited {
  color: var(--primary-color);
}

svg {
  flex-shrink: 0;
  fill: currentColor;
  transition: fill 0.2s;
}

img {
  border-style: none;
  max-width: 100%;
  display: block;
}

img[data-sizes=auto] {
  display: block;
  width: 100%;
}

img.lazyload:not([src]) {
  visibility: hidden;
}

table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}

th {
  font-weight: 400;
}

td,
th {
  text-align: left;
  vertical-align: top;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.no-scroll {
  overflow-y: scroll;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.no-scroll body {
  height: auto;
  overflow: hidden;
}

::selection {
  color: white;
  background-color: var(--primary-color);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  background-color: transparent;
}

.container {
  width: 100%;
  max-width: calc(var(--max-container-width) + 2 * var(--content-padding));
  margin: 0 auto;
  padding: 0 var(--content-padding);
}
@supports (width: max(20vw, 100px)) {
  .container {
    padding-left: max(env(safe-area-inset-left, 1.4rem), var(--content-padding));
    padding-right: max(env(safe-area-inset-right, 1.4rem), var(--content-padding));
  }
}

.page-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.blue-small-btn {
  font-style: normal;
  font-weight: 600;
  font-size: 1.5rem;
  align-self: flex-start;
  line-height: 100%;
  text-transform: uppercase;
  display: inline-block;
  padding: 2rem 3rem;
  background-color: var(--primary-color);
  color: white;
  transition: background-color 0.2s, color 0.2s;
  border-radius: 1.5rem;
  flex-shrink: 0;
  white-space: nowrap;
}
@media only screen and (max-width: 640px) {
  .blue-small-btn {
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
    padding: 1.5rem 3rem;
    border-radius: 1rem;
  }
}
